<template>
  <div class="modal-dialog modal-xl">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 id="productModalLabel" class="modal-title">
                <span v-if="isNew" ></span>
                <span v-else></span>
              </h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                    <div class="mb-3">
                      <label for="imageUrl" class="form-label"></label>
                      <input type="text" class="form-control"
                             v-model="temp.imageUrl" id="imageUrl">
                      <img :src="temp.imageUrl" class="img-fluid" alt="">
                  </div>
                   <div class="mb-3">
                     <h3></h3>
                     <!--  -->
                     <div v-if="Array.isArray(temp.imagesUrl)">
                       <!-- key  -->
                       <template v-for="(img , key) in temp.imagesUrl" :key="key + '123'">
                       <input type="text" class="form-control" v-model="temp.imagesUrl[key]" >
                       <img :src="temp.imagesUrl[key]" class="img-fluid" alt="">
                       </template>
                       <!-- imagesUrl imagesUrl -->
                       <button v-if="!temp.imagesUrl.length || temp.imagesUrl[temp.imagesUrl.length-1]" type="button" class="btn btn-primary me-1" @click="temp.imagesUrl.push('')"></button>
                       <!--  -->
                       <button v-else type="button" class="btn btn-outline-danger" @click="temp.imagesUrl.pop()"></button>
                     </div>
                   </div>
                  </div>
                <div class="col-sm-8">
                  <div class="mb-3">
                    <label for="title" class="form-label"></label>
                    <input id="title" type="text" class="form-control" placeholder="" v-model="temp.title">
                  </div>

                  <div class="row">
                    <div class="mb-3 col-md-6">
                      <label for="category" class="form-label"></label>
                      <input id="category" type="text" class="form-control"
                             placeholder="" v-model="temp.category" >
                    </div>
                    <div class="mb-3 col-md-6">
                      <label for="price" class="form-label"></label>
                      <input id="unit" type="text" class="form-control" placeholder="" v-model="temp.unit">
                    </div>
                  </div>

                  <div class="row">
                    <div class="mb-3 col-md-6">
                      <label for="origin_price" class="form-label"></label>
                      <input id="origin_price" type="number" min="0" class="form-control" placeholder="" v-model.number="temp.origin_price">
                    </div>
                    <div class="mb-3 col-md-6">
                      <label for="price" class="form-label"></label>
                      <input id="price" type="number" min="0" class="form-control"
                             placeholder="" v-model.number="temp.price">
                    </div>
                  </div>
                  <hr>

                  <div class="mb-3">
                    <label for="description" class="form-label"></label>
                    <textarea id="description" type="text" class="form-control"
                              placeholder=""  v-model="temp.description">
                    </textarea>
                  </div>
                  <div class="mb-3">
                    <label for="content" class="form-label" ></label>
                    <textarea id="description" type="text" class="form-control"
                              placeholder=""  v-model="temp.content">
                    </textarea>
                  </div>
                  <div class="mb-3">
                    <div class="form-check">
                      <input id="is_enabled" class="form-check-input" type="checkbox"
                             :true-value="1" :false-value="0" v-model="temp.is_enabled">
                      <label class="form-check-label" for="is_enabled"></label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
                
              </button>
              <button type="button" class="btn btn-primary" @click="updateProducts">
                
              </button>
            </div>
          </div>
        </div>
</template>

<script>
export default {
  data () {
    return {
      isNew: false,
      temp: {}
    }
  }
}
</script>
