<template>
  <h2></h2>
  <div class="container">
    <div class="text-end mt-4">
      <!-- modal -->
      <button class="btn btn-primary" type="button" @click="openModal('new')">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td>{{ item.origin_price }}</td>
          <td>{{ item.price }}</td>
          <td>
            <span class="text-success" v-if="item.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-outline-primary btn-sm"
                @click="openModal('edit', item)"
              >
                
              </button>
              <button
                type="button"
                class="btn btn-outline-danger btn-sm"
                @click="openModal('delete', item)"
              >
                
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <!--    -->
    <Pagination
      :pages="pagination"
      @get-productlist="getProductsList"
    ></Pagination>
  </div>
  <!-- /Modal -->
  <div
    id="productModal"
    ref="productModal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="productModalLabel"
    aria-hidden="true"
  >
    <!-- & -->
    <ProductModal
      :temp="temp"
      :is-new="isNew"
      @update="getProductsList"
      :current-page="pagination.current_page"
    ></ProductModal>
  </div>
  <!--Modal -->
  <div
    id="delProductModal"
    ref="delProductModal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="delProductModalLabel"
    aria-hidden="true"
  >
    <DeleteModal :item="temp" @update="getProductsList"></DeleteModal>
  </div>
</template>

<script>
import DeleteModal from '@/components/DelModal.vue'
import Pagination from '@/components/Pagination.vue'
import ProductModal from '@/components/ProductModal.vue'
export default {
  components: {
    Pagination,
    ProductModal,
    DeleteModal
  },
  data () {
    return {
      products: [],
      isNew: false, // ormodal
      temp: {
        imagesUrl: []
      },
      pagination: {}
    }
  },
  methods: {
    //   //
    getProductsList (page = 1) {
      const getProductsUrl = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products/?page=${page}`
      this.$http.get(getProductsUrl).then((res) => {
        this.products = res.data.products
        this.pagination = res.data.pagination
      })
    },
    // modal
    openModal (type, product) {
      if (type === 'new') {
        //  temp
        this.temp = {
          imagesUrl: []
        }
        // modal
        this.isNew = true
        // modal
        ProductModal.show()
      } else if (type === 'edit') {
        // 
        this.temp = JSON.parse(JSON.stringify(product)) //  product.imagesUrl
        // 
        if (!this.temp.imagesUrl) {
          this.temp.imagesUrl = []
        }
        // modal
        this.isNew = false
        ProductModal.show()
      } else if (type === 'delete') {
        this.temp = { ...product } // modal
        DeleteModal.show()
      }
    }
  },
  mounted () {
    this.getProductsList()
  }
}
</script>
